import { ICommunicationHandler } from '../../../infrastructure/communication/communication-handler';
import { HtmlHelpers, NumberHelpers } from '../helpers';
import { Visibility } from './visibility';
import { MfdStatusType, MfdStatusUpdateDataContent, StatusUpdateDataContent } from '../../../infrastructure/communication/protocol';

export abstract class Component {
    public isUnavailable: boolean = false;
    public hideOnServiceUnavailable: boolean = false;
    protected element: HTMLElement;
    protected communicationHandler: ICommunicationHandler;
    private _visibility: Visibility = null; // Whether it should be visible or not. Is initially null to force a visibility check on first call.
    private _isVisible: boolean = null; // If the element is currently visible. Is initially null to force a visibility check on first call.
    private _layerId: string;
    private _mfdComponentId: number;
    private _nmeaComponentId: number;

    constructor(element: HTMLElement, communicationHandler: ICommunicationHandler) {
        this.element = element;
        this._layerId = this.element.dataset.layer;
        this.communicationHandler = communicationHandler;
        this._mfdComponentId = parseInt(this.element.dataset.ebpComponentId, 10); // ebp-component-id
        this._nmeaComponentId = parseInt(this.element.dataset.nmeaComponentId, 10); // nmea-component-id
        if (!isNaN(this._mfdComponentId)) {
            this.communicationHandler.addOnMfdValueReceivedDelegate(this._mfdComponentId, (value: MfdStatusUpdateDataContent) => this._onMfdDataReceived(value));
        }
        if (!isNaN(this._nmeaComponentId)) {
            this.communicationHandler.addOnNmeaValueReceivedDelegate(this._nmeaComponentId, (value: StatusUpdateDataContent) => this.onNmeaDataReceived(value));
        }
    }

    public initialize(): void {
        this.hideOnServiceUnavailable = NumberHelpers.parseBooleanArgument(this.element.dataset.hideOnServiceUnavailable);
        this.onInitialize();
    }
    get layerId(): string {
        return this._layerId;
    }

    public setVisibility(visibility: Visibility): void {
        if (visibility !== this._visibility) {
            this._visibility = visibility;
            this._evaluateVisbility();
        }
    }
    public refresh(): void {
        if (this._visibility) {
            this.onRedraw();
        }
    }

    public abstract onRedraw(): void;

    // End virtual methods
    protected abstract onInitialize(): void;

    get mfdComponentId(): number {
        return this._mfdComponentId;
    }
    get nmeaComponentId(): number {
        return this._nmeaComponentId;
    }


    public abstract allowSignalTypes(): MfdStatusType[];
    // Virtual methods
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onMfdDataReceived(_message: MfdStatusUpdateDataContent): void { }
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onNmeaDataReceived(_message: StatusUpdateDataContent): void { }
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onVisibilityChanged(_visible: boolean): void { }
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    public onUnavailableChanged(): void { }

    private _onMfdDataReceived(message: MfdStatusUpdateDataContent): void {

        if (this.allowSignalTypes().length > 0 && !this.allowSignalTypes().includes(message.type)) {
            return;
        }

        if (this.isUnavailable !== message.unavailable) {
            this.isUnavailable = message.unavailable;
            this.onUnavailableChanged();
            this._evaluateVisbility();
        }

        this.onMfdDataReceived(message);
    }
    private _evaluateVisbility(): void {

        const visible = this._visibility === Visibility.visible ? !(this.isUnavailable && this.hideOnServiceUnavailable) : false;
        if (visible !== this._isVisible) {
            this._isVisible = visible;
            HtmlHelpers.setElementVisibility(this.element, visible);
            this.onVisibilityChanged(visible);
            this.refresh();
        }
    }
}
